
import { defineComponent } from "vue";

import { DEBUG } from "@/constants/";
import { COLOR_PRESETS } from "@/constants/appearance";
import {
  COLOR_HELP,
  INITIAL_RESULT,
  LOGO_COLOR_HELP,
  LOGO_HELP,
} from "@/constants/text";

import Checkbox from "@/components/Checkbox.vue";
import Help from "@/components/Help.vue";
import Results from "@/components/Results.vue";
import TextInput from "@/components/TextInput.vue";

import { genericBadge } from "@/core/genericBadge";

const note = `
This form lets you create a fixed badge using arbitrary text and a link.

For example, describe a tools or platforms your repo is built on or built for running. This form takes care of encoding characters so they are safe for the badge URL.
`;

export default defineComponent({
  name: "GenericBadges",
  components: {
    Checkbox,
    Help,
    Results,
    TextInput,
  },
  data() {
    return {
      label: "Foo",
      message: "Bar",
      badgeColor: COLOR_PRESETS.Green,
      isLarge: false,
      target: "https://",
      logo: "",
      logoColor: "",

      result: INITIAL_RESULT,

      note: note,
      colorHelp: COLOR_HELP,
      logoHelp: LOGO_HELP,
      logoColorHelp: LOGO_COLOR_HELP,
    };
  },
  methods: {
    submit() {
      if (DEBUG) {
        console.debug("Process inputs and render results");
        console.debug({
          label: this.label,
          message: this.message,
          badgeColor: this.badgeColor,
          isLarge: this.isLarge,
          target: this.target,
          logo: this.logo,
          logoColor: this.logoColor,
        });
      }

      const dashBadge = genericBadge(
          this.label,
          this.message,
          this.badgeColor,
          this.isLarge,
          this.target,
          this.logo,
          this.logoColor,
          false
        ),
        paramBadge = genericBadge(
          this.label,
          this.message,
          this.badgeColor,
          this.isLarge,
          this.target,
          this.logo,
          this.logoColor,
          true
        );

      this.result = `\
_Dash badge_

${dashBadge}

_Query parameter badge_

${paramBadge}
      `;
    },
  },
});
